<template>
	<div id="container"></div>
</template>
<style  scoped>
.amap-container {
	overflow: hidden;
	touch-action: none;
	position: relative;
	background: rgb(252, 249, 242);
	cursor: pointer;
}
#container {
	padding: 0px;
	margin: 0px;
	width: 100%;
	height: 800px;
}

.amap-info-window {
	width: 400px;
	background: #fff;
	color: black;
	border-radius: 3px;
	padding: 3px 7px;
	border: 1px solid grey;
	position: relative;
}
.amap-info-sharp {
	position: absolute;
	top: 21px;
	bottom: 0;
	left: 50%;
	margin-left: -8px;
}
</style>
<script>
import AMapLoader from '@amap/amap-jsapi-loader';

import { points, icons, iconList, icon } from '@/http/points'
// import { mapList } from '@/http/mapList'




import { fetchList } from '@/http/api'
export default {
	data() {
		return {
			map: null,
			LabelsData: [],
			tempData: [],
			today: new Date().toLocaleDateString().split('/').map(item => { if (item < 10) { return '0' + item } else { return item } }).join('-')
		}
	},
	mounted() {
		//DOM初始化完成进行地图初始化

		this.getList()
		// console.log(mapList.length)
		// this.initMap();

	},
	methods: {
		getList() {
			// this.$axios.get('/address/list').then(res => {
			// 	console.log(res)
			// })
			fetchList().then(res => {
				this.LabelsData = res.data
				this.$nextTick(() => {
					this.initMap()
				})

			})
		},
		initMap() {
			let that = this
			let now = new Date()

			var textStyle = {
				fontSize: 12,
				fontWeight: 'normal',
				fillColor: '#22886f',
				strokeColor: '#fff',
				strokeWidth: 2,
				fold: true,
				padding: '2, 5',
			};




			AMapLoader.load({
				key: "693db22c8972aedfbea20a27687e6bba",             // 申请好的Web端开发者Key，首次调用 load 时必填
				version: "2.0",      // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
				plugins: [''],       // 需要使用的的插件列表，如比例尺'AMap.Scale'等
			}).then((AMap) => {

				this.map = new AMap.Map("container", {  //设置地图容器id
					// viewMode: "3D",    //是否为3D地图模式
					zoom: 12,
					center: [116.397045, 39.909729],
					showIndoorMap: false,
					viewMode: '2D',
					expandZoomRange: true,
				});

				var layer = new AMap.LabelsLayer({
					zooms: [3, 20],
					zIndex: 100,
					// 关闭标注避让，默认为开启，v1.4.15 新增属性
					animation: false,
					// 关闭标注淡入动画，默认为开启，v1.4.15 新增属性
					collision: false
				})

				// 图层添加到地图
				this.map.add(layer);

				var markers = [];
				// let LabelsData = points
				// alert(3)
				// let LabelsData = mapList

				let LabelsData = this.LabelsData
				// let LabelsData = this.tempData

				console.log(LabelsData)


				var infoWindow = new AMap.InfoWindow({ offset: new AMap.Pixel(0, -8) });

				// 初始化 labelMarker
				for (var i = 0; i < LabelsData.length; i++) {

					// var curData = LabelsData[i]
					// curData.position = LabelsData[i].position
					// curData.name = LabelsData[i].name
					// curData.icon = icons[curData.aa]

					var curData = {}
					let pos1 = parseFloat(LabelsData[i].longitude), pos2 = parseFloat(LabelsData[i].latitude)
					let name = LabelsData[i].text

					let isPeriod = name.indexOf("高峰期")
					curData.position = [pos1, pos2]
					curData.name = name
					curData.createTime = LabelsData[i].createTime
					// curData.icon = iconList[0]

					// console.log(curData)
					if (curData.type == 5) { // 进京证&高峰期
						curData.icon = iconList[2]
					} else if (isPeriod > 0) { // 高峰期
						curData.icon = iconList[1]
					} else if (this.today == curData.createTime) { // 当天的
						curData.icon = iconList[3]
					} else {
						curData.icon = iconList[0]
					}
					curData.extData = {
						index: i
					}

					var labelMarker = new AMap.LabelMarker(curData)

					// 事件
					labelMarker.on('click', function (e) {
						var position = e.data.data && e.data.data.position;

						if (position) {
							infoWindow.setContent(e.target.content);
							infoWindow.open(that.map, e.target.getPosition());
						}
					})


					labelMarker.content = (
						"<div class ='area'>" + curData.name + "</div>");
					labelMarker.on('mouseout', function () {
						// 鼠标移走
					});

					markers.push(labelMarker);

					// labelMarker.on('click', (e) => {
					// 	alert(1)
					// 	var position = e.data.data && e.data.data.position;
					// 	if (position) {
					// 		infoWindow.setAnchor('bottom-center')
					// 		infoWindow.setContent(e.target.content);
					// 		infoWindow.open(this.map, e.target.getPosition());
					// 	}
					// });
					// labelMarker.content = ("<div class ='area'>" + curData.name + "</div>")
					// 事件
					// labelMarker.on('mouseout', () => {
					// 	infoWindow.close();

					// });

					// markers.push(labelMarker);

				}
				// 将 marker 添加到图层
				layer.add(markers);

				// this.map.on('zoomend', () => {
				// 	console.log('缩放级别', this.map.getZoom())
				// })


			}).catch(e => {
				console.log(e);
			})
		},
	},

}
</script>


